/* Each Section */
section {
  margin-bottom: 2.5rem;
  border-bottom: 1px solid #e5e7eb;
  padding-bottom: 2rem;
}

section:last-child {
  border-bottom: none;
}

/* Section Heading */
section h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: #1d4ed8;
  border-left: 5px solid #3b82f6;
  padding-left: 0.75rem;
  font-weight: 600;
}

/* Grid Layout for Inputs */
.section-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
}

/* Label and Input Structure */
label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: #374151;
}

input,
select,
textarea {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 1rem;
  transition: border-color 0.3s, box-shadow 0.3s;
  background-color: #f9fafb;
}

.section-grid-personal {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.section-grid-personal .col-span-3 {
  grid-column: span 3;
}


input:focus,
select:focus,
textarea:focus {
  outline: none;
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.2);
}

/* Responsive Textarea */
textarea {
  min-height: 120px;
  resize: vertical;
}

/* Submit Button Styling */
button[type="submit"] {
  display: block;
  margin-top: 2rem;
  padding: 0.75rem 2rem;
  background-color: #2563eb;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button[type="submit"]:hover {
  background-color: #1e40af;
}
